<template>
<div class="mt-3">
    <v-card>

        <v-card-title>
            <div class="headline">{{ data.user }}</div>
            <div class="md-2">написал  {{ created_at }}</div>
            <v-spacer/>
            <Like :content="data"/>
        </v-card-title>

        <v-divider/>
        <edit-reply
            v-if="editing"
            :data=data
        />

        <v-card-text v-else v-html="reply"></v-card-text>

        <v-divider/>

        <div v-if="!editing">
            <v-card-actions v-if="own">
                <v-btn icon small @click="edit">
                    <v-icon color="orange">edit</v-icon>
                </v-btn>
                <v-btn icon small @click="destroy">
                    <v-icon color="red">delete</v-icon>
                </v-btn>
            </v-card-actions>
        </div>

    </v-card>
</div>
</template>

<script>
import editReply from "./editReply";
import Like from "@/components/likes/like";
import { mapGetters } from "vuex";

export default {
  name: "reply",
  props: ["data", "index"],
  components: { editReply, Like },
  data() {
    return {
      editing: false
    };
  },
  computed: {
    ...mapGetters(["login/id", "login/loggedIn"]),
    own() {
      return this["login/id"] == this.data.user_id;
    },
    reply() {
      return md.parse(this.data.reply);
    },
    created_at() {
      const t = new Date(this.data.created_at).toLocaleString();
      return t != "Invalid Date" ? t : "";
    }
  },
  created() {
    this.listen();
  },
  methods: {
    destroy() {
      EventBus.$emit("deleteReply", this.index);
    },
    edit() {
      this.editing = true;
    },
    listen() {
      EventBus.$on("cancelEditing", () => {
        this.editing = false;
      });
    }
  }
};
</script>
